import request from "@/utils/request";
let test = true;
export function fetchStatisticsList(params) {
  if (test) {
    // console.log(params)
    let max = 5000;
    let statisticsList = {}
    if (params.graphType == 1) {
      statisticsList = {
        code: 200,
        message: "success",
        data: {
          list: [{
            date: '2018-11-01',
            orderCount: 10,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-11-02',
            orderCount: 20,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-11-03',
            orderCount: 33,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-11-04',
            orderCount: 50,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-11-05',
            orderCount: 80,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }]
        }
      };
    }
    if (params.graphType == 2) {
      statisticsList = {
        code: 200,
        message: "success",
        data: {
          list: [{
            date: '2018-10-01',
            orderCount: 10,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-10-02',
            orderCount: 20,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-10-03',
            orderCount: 33,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }]
        }
      };
    }
    if (params.graphType == 3) {
      statisticsList = {
        code: 200,
        message: "success",
        data: {
          list: [{
            date: '2018-01',
            orderCount: 10,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-02',
            orderCount: 20,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-03',
            orderCount: 33,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-04',
            orderCount: 50,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-05',
            orderCount: 80,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-06',
            orderCount: 80,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }, {
            date: '2018-07',
            orderCount: 80,
            orderAmount: parseInt(Math.random() * max, 10) + 1
          }]
        }
      };
    }
    return new Promise(resolve => {
      resolve(statisticsList)
    })
  }
  return request({
    url: '/statistics/list',
    method: 'get',
    params: params
  })
}

export function fetchHomeInfo() {
  if (test) {
    let statisticsInfo = {
      code: 200,
      message: "success",
      data: {
        orderCount: 100,
        orderAmount: 10000,
        oldOrderAmount: 8000
      }
    };
    return new Promise(resolve => {
      resolve(statisticsInfo)
    })
  }
  return request({
    url: '/statistics/homeInfo',
    method: 'get'
  })
}



